@import "https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&family=Inter:wght@400;700&display=swap";
:root {
  --font-size: 1rem;
  --spacing: 2rem;
  --sans-serif: "Inter", sans-serif;
  --mono: "IBM Plex Mono", monospace;
  --fadeIn: fadeIn .3s ease;
  --gray-10: #14181f;
  --gray-14: #1d212b;
  --white-98: #fafafa;
  --blue-50: #00f;
  --purple-25: purple;
  --spacing-1: .4rem;
  --spacing-2: .8rem;
  --radius: 4px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff5ee;
  font-family: var(--sans-serif);
  font-size: var(--font-size);
  display: flex;
  overflow: hidden;
}

.logs {
  color: #1f1f1f;
  opacity: .5;
  background-color: wheat;
  border: none;
  border-radius: 4px;
  margin-right: 2em;
  padding: .4rem .8rem;
  font-size: .8em;
  font-weight: 500;
  position: fixed;
  right: 0;
}

.logs:hover {
  opacity: 1;
}

.code, .result {
  height: 100vh;
  width: 100%;
}

.button-container {
  margin-left: var(--spacing-2);
  position: absolute;
}

.fullscreen {
  padding: var(--spacing-1) var(--spacing-2);
  margin-top: var(--spacing-2);
  color: var(--white-98);
  background-color: var(--gray-10);
  border-radius: var(--radius);
