}

.buttons-hidden {
  display: none;
}

.editor-container {
  height: 100%;
  width: 100%;
  resize: horizontal;
  background-color: #2e3440;
  overflow: hidden;
}

.editor-menu {
  margin-left: 1em;
  position: absolute;
  bottom: 0;
}

.editor {
  height: 50%;
  width: 100%;
  resize: vertical;
  overflow: hidden;
}

.result {
  flex-direction: column;
  display: flex;
}

.output, .source {
  height: 50%;
  background-color: #1f1f1f;
  position: relative;
  overflow-x: hidden;
}

.shrink {
  height: 10%;
}

.expand {
  height: 100%;
}

iframe {
  height: 100%;
  width: 100%;
  animation: var(--fadeIn);
  border: none;
}

.errors {
  padding: var(--spacing);
  color: tomato;
  animation: var(--fadeIn);
}

.errors xmp {
  max-width: 80%;
}

.source {
  padding: 1rem var(--spacing);
  animation: var(--fadeIn);
  border-top: 2px solid #1a1a1a;
  line-height: 1.3;
}

pre, xmp {
  font-family: var(--mono);
  white-space: pre-wrap;
  word-wrap: break-word;
}

@media (width <= 800px) {
  body {
    display: block;
    overflow: scroll;
  }

  .code, .result {
    height: 50vh;
  }

  .editor {
    resize: none;
  }

  .output {
    height: 100%;
  }

  .source {
    display: none;
  }
}

.loading {
  z-index: 1;
  background-color: #1d262b;
  place-content: center;
  gap: 1rem;
  display: grid;
  position: absolute;
  inset: 0;
}

.modal {
  width: 50%;
  height: 50%;
  background-color: var(--gray-10);
  border-radius: var(--radius);
  z-index: 1;
  background-color: #1d262b;
  border: none;
  place-content: center;
  gap: 1rem;
  margin: auto;
  display: grid;
  position: absolute;
  inset: 0;
}

.loader {
  width: 40px;
  height: 40px;
  background: linear-gradient(to right, #fff 10%, #fff0 42%);
  border-radius: 50%;
  margin: 0 auto;
  animation: 1.4s linear infinite spin;
  transform: translateZ(0);
}

.loader:before {
  content: "";
  height: 50%;
  width: 50%;
  background-color: #fff;
  border-radius: 100% 0 0;
  position: absolute;
  inset: 0;
}

.loader:after {
  content: "";
  height: 75%;
  width: 75%;
  background-color: #1d262b;
  border-radius: 50%;
  margin: auto;
  position: absolute;
  inset: 0;
}

@keyframes spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.editor-menu button {
  padding: var(--spacing-1) var(--spacing-2);
