  cursor: pointer;
  opacity: .5;
  border: none;
  font-size: .8em;
  font-weight: 500;
  position: relative;
}

.modal {
  width: auto;
}

.modal-clicklistener {
  width: 100%;
  height: 100%;
  position: absolute;
}

.modal table .row {
  cursor: pointer;
  width: 100%;
  border-radius: var(--radius);
  border: none;
  margin-bottom: .5em;
  margin-left: -.6em;
  padding: .6em;
  display: block;
  position: relative;
}

.modal table .row:hover {
  background-color: #2e3440;
}

.modal table tr td {
  padding-right: 2em;
